@import url('https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap');

* {
    font-family: 'RocknRoll One', sans-serif;
}

.App {
    text-align: center;
    background-color: #FFF;
    width: 100vw;
    height: 100vh;
    color: #000;
}

body {
    padding: 0%;
    margin: 0;
}

nav {
    height: 50px;
    width: 100%;
    margin: auto;
    border-bottom: 1px solid #d3d6da;
    display: grid;
    place-items: center;
}

nav h1 {
    margin: auto;
    color: #000;
    font-size: 35px;
    text-align: center;
}

h2 {
    margin: 15px;
}

.game {
    width: 100%;
    max-width: 500px;
    height: calc(100% - 55px);

    margin: 0 auto;
    display: flex;
    flex-direction: column;
    
    justify-content: space-between;
    /* align-items: center; */
}

@media screen and (max-width: 500px) {
    .App {
        height: auto !important;
    }

    .game {
        height: auto !important;
    }
}

.w {
    padding: 10px 0;
    height: 100%;
}

.ImageBox {
    display: flex;
    justify-content: center;
    align-items: center;
    /* flex-grow: 1; */
    overflow: hidden;
    /* background: #d3d6da; */
    height: 350px;
}

.Board {
    padding: 10px 20px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 5px;
}

.Guess {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 15px 0;
    /* background: #2b75ff; */
}

.square {
    border: 2px solid #d3d6da;
    width: 100%;
    height: 30px;
    box-sizing: border-box;
    /* display: inline-flex; */
}

.square::before {
    content: '';
    padding-bottom: 100%;
}

.inputrow {
    width: 95%;
    display: flex;
    margin: 5px;
}

.selectlist {
    display: grid!important;
    grid-template-columns: 2fr 3fr 1fr;
    grid-gap: 5px;
}

.selectlist > button {
    background-color: #fff;
    border: 1px solid #2b75ff;
    border-radius: 5px;
    color: #000;
    padding: 5px;
    font-size: medium;
}

.selectlist > select {
    border-radius: 5px;
    width: 100%;
    font-size: medium;
    border: 1px solid rgb(43 117 255);
}

.selectlist > select:active{
    border: 1px solid rgb(43 117 255);
}

.submitlist {
    justify-content: space-evenly;
}

.submitlist > button {
    width: 45%;
    background: #FFF;
    border: 1px solid rgb(43 117 255);
    border-radius: 5px;
    font-size: large;
    box-shadow: 0 2px 2px rgb(43 117 255 / 80%);
    padding: 10px 0;
}

.submitlist > button:active{
    background: rgb(43 117 255);
    color: #FFF;
}

.Sakura {
    background: rgb(43 117 255)!important;
    color: #FFF!important;
}

#correct {
    border:#6aaa64;
    background: #6aaa64!important;
}

#almost {
    border:#c9b458;
    background: #c9b458!important;
}

#error {
    border: #86888a;
    background: #86888a!important;
}

.overlay {
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    background-color:rgba(255, 255, 255, 0.5);;
    z-index: 3000;
}

.show {
    display:flex;
    position: fixed;
}

.content {
    display: flex;
    position: relative;
    border-radius: 8px;
    border: 1px solid #f6f7f8;
    background-color: #ffffff;
    box-shadow: 0 4px 23px 0 rgb(0 0 0 / 20%);
    width: 90%;
    max-height: 90%;
    overflow-y: auto;
    animation: SlideIn 200ms;
    max-width: 500px;
    padding: 16px;
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@keyframes SlideIn {
    0% {
        transform: translateY(30px);
        opacity: 0;
    }
    100% {
        transform: translateY(0px);
        opacity: 1;
    }
}

.closebtn{
    width: 35px;
    height: 35px;
    position: absolute;
    top: 16px;
    right: 16px;
    background: #FFF;
    border:0;
    cursor: pointer;
}

.content > img {
    width: 95%;
}

.cardname {
    padding: 15px 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.btnlist {
    width: 95%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    justify-items: center;
}

.sharebtn {
    background: #6aaa64;
    color: #FFF;
    font-weight: bold;
    border-radius: 4px;
    cursor: pointer;
    border: none;
    user-select: none;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    width: 95%;
    font-size: 15px;
    height: 50px;
    margin: 5px;
}

.sharebtn > span {
    padding: 0 15px;
}

.sharebtn:hover {
    background: rgb(106, 170, 100, .8);
}

.tweet {
    background: #1DA1F2!important;
}

.tweet:hover {
    background: rgb(29, 161, 242, .8)!important;
}

.toaster {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translate(-50%, 0);
    pointer-events: none;
    width: fit-content;
    z-index:5000;
}

.toaster-content {
    display: flex;
    position: relative;
    border-radius: 8px;
    border: 1px solid #ffc107;
    background-color: #ffc107;
    color: #f6f7f8;

    box-shadow: 0 4px 23px 0 rgb(0 0 0 / 20%);
    width: 100%;
    max-height: 90%;
    overflow-y: auto;
    padding: 16px;
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    animation: SlideIn 200ms;
}
